import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { v4 } from 'uuid';
import styles from './ContactForm.module.css';
import actions from '../../redux/contacts/contacts-actions';

class ContactForm extends Component {
  static propTypes = {
    handleSubmit: PropTypes.func,
  };

  state = {
    name: '',
    number: '',
  };

  handleChange = event => {
    this.setState({ [event.currentTarget.name]: event.currentTarget.value });
  };

  handleSubmit = event => {
    event.preventDefault();

    const newContact = {
      id: v4(),
      name: this.state.name,
      number: this.state.number,
    };

    this.props.addContact(newContact);
    this.setState({ name: '' });
    this.setState({ number: '' });
  };

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit} className={styles.form}>
          <label className={styles.label}>
            Name
            <input
              type="text"
              name="name"
              pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
              title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
              required
              value={this.state.name}
              onChange={this.handleChange}
              className={styles.input}
            />
          </label>
          <label className={styles.label}>
            Phone number
            <input
              type="tel"
              name="number"
              pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
              title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
              required
              value={this.state.number}
              onChange={this.handleChange}
              className={styles.input}
            />
          </label>
          <button type="submit" className={styles.button}>
            Add contact{' '}
            <span role="img" aria-label="green check" className={styles.icon}>
              ‚úÖ
            </span>
          </button>
        </form>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addContact: newContact => dispatch(actions.addContact(newContact)),
  };
};

export default connect(null, mapDispatchToProps)(ContactForm);
